.checkout_Product{
    display: flex; 
    margin-top: 20px;
    margin-bottom: 20px;
}

.checkoutProduct_image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkProduct_info{
    padding-left: 20px;
}
.checkProduct_info>button{
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.checkProduct_info>button:hover{
    background-color: #d9b540;
}
.product__rating1{
    display: flex;
}
.checkProduct_title{
    font-size: 17px;
    font-weight: 800;
}