.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top:0;
    z-index: 100;
}
.header__logo{
    width:100px;
    object-fit: contain;
    margin-top: 18px;
    margin-left: 20px;
    margin-right: 20px;
}
.header__search{
    display: flex;
    flex:1;
}
.header__searchinput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header__searchicon{
    padding: 5px;
    height: 22px !important;
    background-color:orange;
}
.header__nav{
    display: flex;
    justify-content: space-evenly;
}
.header__link{
    color :white;
    text-decoration: none;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-left: 10px;
}
.header__option1{
    font-size: 10px;
}
.header__option2{
    font-size: 13px;
    font-weight: 800;
}
.header__optionbasket{
    display: flex;
    /* color: white; */
    align-items: center;
    /* text-decoration: none; */
    /* color: white; */
}
.header__basketcount{
    margin-left: 10px;
    margin-right: 10px;
    color: white;
}